import Daily from "@daily-co/daily-js";
import VideoTile from "./videoTile";
import AudioTile from "./audioTile";

class RoomManager {
  #config = null;
  #chat = null;
  #agentVideoTrack = null;
  #agentAudioTrack = null;
  #agentAudioTile = null;
  #agentSpeakingStatus = null;
  #onMessage = () => {};
  #onAgentEnter = () => {};

  #onClose = () => {};
  #onError = () => {};
  #onAgentSpeakingStart = () => {};
  #onAgentSpeakingEnd = () => {};

  #agentVideoTile = null;
  #userParticipantId = null;
  #agentParticipantId = null;

  constructor(config) {
    if (RoomManager.instance) {
      return RoomManager.instance;
    }
    // For Manage Room Property
    this.#config = config;
    this.#chat = null;

    // custom event handlers
    this.#onMessage = () => {};
    this.#onAgentEnter = () => {};

    this.#onClose = () => {};
    this.#onError = () => {};
    this.#onAgentSpeakingStart = () => {};
    this.#onAgentSpeakingEnd = () => {};

    // for manage participants
    this.#userParticipantId = null;
    this.#agentParticipantId = null;
    this.#agentSpeakingStatus = null;

    // store latest tracks
    this.#agentVideoTrack = null;
    this.#agentAudioTrack = null;

    // video, audio tiles
    this.#agentVideoTile = null;
    this.#agentAudioTile = null;

    this.#initialize();

    RoomManager.instance = this;
    return this;
  }

  #initialize() {
    this.#createCallObject();
    this.#setupEventListeners();
  }

  #setupEventListeners() {
    const event = {
      "joined-meeting": this.#handleJoin.bind(this),
      "left-meeting": this.#handleLeave.bind(this),
      "participant-joined": this.#handleParticipantJoinedOrUpdated.bind(this),
      "participant-updated": this.#handleParticipantJoinedOrUpdated.bind(this),
      "app-message": this.#handleOnMessage.bind(this),
    };
    Object.entries(event).forEach(([event, handler]) => {
      this.#chat.on(event, handler);
    });
  }
  // ---- [Set Custom Event Handlers] -----------------------------------------
  setOnMessage(handler) {
    this.#onMessage = handler;
  }
  setOnAgentEnter(handler) {
    this.#onAgentEnter = handler;
  }
  setOnError(handler) {
    this.#onError = handler;
  }
  setOnAgentSpeakingStart(handler) {
    this.#onAgentSpeakingStart = handler;
  }
  setOnAgentSpeakingEnd(handler) {
    this.#onAgentSpeakingEnd = handler;
  }
  setOnClose(handler) {
    this.#onClose = handler;
  }
  // ---- [Event Handlers] ------------------------------------------------
  #handleJoin = (event) => {
    const isLocal = event.participants.local;
    const participantId = event.participants.local.session_id;
    if (isLocal && !this.isUserExists()) {
      //joined-meeting 이벤트에서만 user participant id 설정
      this.#enterUser(participantId);
    }
  };
  #handleLeave = (event) => {
    this.#clearUser();
    this.#clearAgent();
  };
  #handleParticipantJoinedOrUpdated = (event) => {
    const isLocal = event.participant.local;
    const participantId = event.participant.session_id;
    if (!isLocal && !this.isAgentExists()) {
      this.#enterAgent(participantId);
      this.#onAgentEnter(); //user-defined event handler
    }

    const tracks = event.participant.tracks;
    Object.entries(tracks).forEach(([trackType, trackInfo]) => {
      const newTrack = trackInfo.persistentTrack;
      if (newTrack) {
        if (isLocal) {
          return;
        } else {
          // Track is Agent's
          if (trackType === "video" && this.#agentVideoTrack !== newTrack) {
            this.#updateAgentVideoTrackAndTile(newTrack);
          }
          if (trackType === "audio" && this.#agentAudioTrack !== newTrack) {
            this.#updateAgentAudioTrackAndTile(newTrack);
          }
        }
      }
    });
  };

  #handleOnMessage = (event) => {
    const text = event.data;

    if (text === "$START$") {
      // when agent starts speaking
      this.#setAgentSpeakingStatus(true);
      this.#onAgentSpeakingStart();
    } else if (text === "$END$") {
      // when agent finishes speaking
      this.#setAgentSpeakingStatus(false);
      this.#onAgentSpeakingEnd();
    } else if (text === "$ERROR$") {
      const error = new Error("$ERROR$ recieved.");
      this.#onError(error);
    } else if (text === "$CLOSE$") {
      this.#onClose();
      this.leave();
    } else {
      this.#onMessage(text); // user-defined message handler
    }
    return "";
  };

  // ---- [Participant Management] ----------------------------------------------------
  #enterUser(participantId) {
    this.#userParticipantId = participantId;
  }
  #enterAgent(participantId) {
    this.#agentParticipantId = participantId;
    this.#setAgentSpeakingStatus(false);
  }
  #clearUser() {
    this.#userParticipantId = null;
  }
  #clearAgent() {
    this.#agentParticipantId = null;
    this.#agentSpeakingStatus = null;
    this.#agentAudioTrack = null;
    this.#agentVideoTrack = null;
    this.clearAgentVideoTile();
  }
  #setAgentSpeakingStatus(status) {
    if (status === true) {
      this.#agentSpeakingStatus = true;
    } else if (status === false) {
      this.#agentSpeakingStatus = false;
    } else {
      console.error("agentSpeakingStatus should be boolean");
    }
  }

  // ---- [Tracks, Video, Audio Tiles Management] -------------------------------------
  addAgentVideoTile(parentSelector = "video-agent") {
    if (!this.#agentVideoTile && this.#agentParticipantId) {
      this.#agentVideoTile = new VideoTile(
        this.#agentParticipantId,
        parentSelector
      );
      this.#agentVideoTile.setTrack(this.#agentVideoTrack);
    }
    this.#addAgentAudioTile();
  }
  #addAgentAudioTile() {
    if (!this.#agentAudioTile && this.#agentParticipantId) {
      this.#agentAudioTile = new AudioTile(this.#agentParticipantId);
      this.#agentAudioTile.setTrack(this.#agentAudioTrack);
    }
  }

  #updateAgentVideoTrackAndTile(newTrack) {
    this.#agentVideoTrack = newTrack;
    if (this.#agentVideoTile) {
      this.#agentVideoTile.setTrack(this.#agentVideoTrack);
    }
  }
  #updateAgentAudioTrackAndTile(newTrack) {
    this.#agentAudioTrack = newTrack;
    if (this.#agentAudioTile) {
      this.#agentAudioTile.setTrack(this.#agentAudioTrack);
    }
  }

  clearAgentVideoTile() {
    if (this.#agentVideoTile) {
      this.#agentVideoTile.clear();
      this.#agentVideoTile = null;
    }
    this.#clearAgentAudioTile();
  }
  #clearAgentAudioTile() {
    if (this.#agentAudioTile) {
      this.#agentAudioTile.clear();
      this.#agentAudioTile = null;
    }
  }

  // ---- [Room Functions] -----------------------------------------------------------------
  #createCallObject() {
    if (Daily.getCallInstance()) {
      Daily.getCallInstance().destroy;
    }
    this.#chat = Daily.createCallObject({
      audioSource: false,
      videoSource: false,
    });
  }
  async #preAuth() {
    await this.#chat.preAuth({
      url: this.#config.url,
      token: this.#config.token,
    });
  }
  async #join() {
    await this.#chat.join({
      startAudioOff: true,
      startVideoOff: true,
    });
  }
  async start() {
    try {
      await this.#preAuth();
      await this.#join();
    } catch (error) {
      console.error("Error ocurred during start:", error);
      this.#onError(error);
      throw error;
    }
  }
  async leave() {
    try {
      if (this.#chat.meetingState() === "joined-meeting") {
        await this.#chat.leave();
      }
    } catch (error) {
      this.#onError(error);
      console.error("Leaving failed", error);
    }
  }
  // ---- [Functions] -------------------------------------------------------------------------
  async sendMessage(text, bgImage, position_x, position_y, scale) {
    if (text === null || text.length === 0) {
      console.error("sendMessage() - text is required.");
      return;
    }
    if (this.#chat && this.#chat.meetingState() === "joined-meeting") {
      const formData = new FormData();
      formData.append("message", text);

      if (bgImage) {
        formData.append("bg_image", bgImage);
      }
      if (position_x != null && position_x.length !== 0) {
        formData.append("position_x", position_x);
      }
      if (position_y !== null && position_y.length !== 0) {
        formData.append("position_y", position_y);
      }
      if (scale !== null && scale.length !== 0) {
        formData.append("scale", Number.parseFloat(scale));
      }

      try {
        const res = await fetch(
          `${this.#config.apiUrl}/rooms/${this.#config.name}/messages`,
          {
            method: "POST",
            headers: {
              "x-api-key": this.#config.apiKey,
            },
            body: formData,
          }
        );
        if (!res.ok) {
          throw new Error("Error during send message");
        }
      } catch (error) {
        console.log(error);
        this.#onError(error);
        throw error;
      }
    }
  }
  userChatState() {
    const status = this.#chat.meetingState();
    if (status === "joining-meeting") {
      return "joining-chat";
    } else if (status === "joined-meeting") {
      return "joined-chat";
    } else if (status === "left-meeting") {
      return "left-chat";
    }
    return status;
  }
  isAgentSpeaking() {
    // if chat is not joined, return null
    return this.#agentSpeakingStatus;
  }
  isUserExists() {
    return this.#userParticipantId ? true : false;
  }
  isAgentExists() {
    return this.#agentParticipantId ? true : false;
  }
}

export default RoomManager;
