class AudioTile {
  constructor(participantId, parentSelector = 'body') {
    this.participantId = participantId;
    this.parentSelector = parentSelector;
    this.track = null;
    this.initialize();
  }
  initialize() {
    this.createAudioElement();
  }
  clear() {
    const selector = `audio-${this.participantId}`;
    const element = document.getElementById(selector);
    if (element) {
      element.srcObject = null;
      element.parentNode.removeChild(element);
    }
  }
  // -------[Create Elements]---------------------------------------
  createAudioElement() {
    const audioEl = document.createElement('audio');
    audioEl.id = `audio-${this.participantId}`;

    if (this.parentSelector) {
      document.querySelector(this.parentSelector).appendChild(audioEl);
    } else {
      document.querySelector('body').appendChild(audioEl);
    }
  }

  // -----[Handle Tracks]--------------------------------------------
  setTrack(track) {
    if (this.track != track) {
      const audioEl = document.getElementById(`audio-${this.participantId}`);

      audioEl.srcObject = new MediaStream([track]);
      audioEl.onloadedmetadata = () => {
        audioEl.play().catch((e) => {
          console.error(
            `Error playing audio for participant ${this.participantId}:`,
            e
          );
        });
      };
      this.track = track;
    }
  }
}
export default AudioTile;
