class VideoTile {
  #track = null;

  constructor(participantId, parentSelector) {
    this.participantId = participantId;
    this.parentSelector = parentSelector;
    this.#track = null;
    this.#initialize();
  }

  #initialize() {
    this.#createVideoContainer();
  }

  clear() {
    const selector = `video-container-${this.participantId}`;
    const element = document.getElementById(selector);
    if (element) {
      element.srcObject = null;
      element.parentNode.removeChild(element);
    }
  }

  // -------[Create Elements]---------------------------------------
  #createVideoContainer() {
    const videoContainerStyle = {
      width: '100%',
      height: '100%',
      boxSizing: 'border-box',
    };
    const videoContainer = document.createElement('div');
    videoContainer.id = `video-container-${this.participantId}`;
    videoContainer.className = 'video-container';
    Object.assign(videoContainer.style, videoContainerStyle);
    const parentEl = document.getElementById(this.parentSelector);

    if (parentEl) {
      parentEl.appendChild(videoContainer);
    } else {
      document.querySelector('body').appendChild(videoContainer);
    }

    const videoElStyle = {
      width: '100%',
      height: '100%',
      objectFit: 'cover',
      boxSizing: 'border-box',
    };
    const videoEl = document.createElement('video');
    videoEl.className = 'video-element';
    Object.assign(videoEl.style, videoElStyle);
    videoContainer.appendChild(videoEl);
  }

  // -------[Handle Tracks]---------------------------------------
  setTrack(track) {
    // track은 항상 유효. (persistentTrack 값임)
    if (this.#track != track) {
      const videoEl = document.querySelector(
        `#video-container-${this.participantId} video.video-element`
      );
      videoEl.srcObject = new MediaStream([track]);
      videoEl.onloadedmetadata = () => {
        videoEl
          .play()
          .catch((e) =>
            console.error(
              `Error playing videoTile for participant ${this.participantId}:`,
              e
            )
          );
      };
      this.#track = track;
    }
  }
}
export default VideoTile;
